import items from './items.json';
import { formatter } from './format';
const cartTemplate = document.querySelector('#cart-template');
console.log(cartTemplate);
let shoppingCart = [];
const cartItemContainer = document.querySelector('[data-cart-items]');
const cartQuantity = document.querySelector('[data-quantity]');

export function addToCart(id) {
  const existingItem = shoppingCart.find((entry) => entry.id === id);
  if (existingItem) {
    existingItem.quantity++;
  } else {
    shoppingCart.push({ id: id, quantity: 1 });
  }
  renderShoppingCart();
}

function renderShoppingCart() {
  const totalCents = shoppingCart.reduce((sum, entry) => {
    const item = items.find((i) => entry.id === i.id);
    return sum + item.priceCents * entry.quantity;
  }, 0);
  cartTotal.innerText = formatter.format(totalCents / 100);

  shoppingCart.forEach((entry) => {
    const item = items.find((i) => entry.id === i.id);

    const cartItem = cartTemplate.content.cloneNode(true);

    const container = cartItem.querySelector('[data-item]');
    container.dataset.itemId = item.id;

    const name = cartItem.querySelector('[data-name]');
    name.innerText = item.name;

    if (entry.quantity > 1) {
      const quantity = cartItem.querySelector('[data-quantity]');
      quantity.innerText = `x${entry.quantity}`;
    }

    const price = cartItem.querySelector('[data-price]');
    price.innerText = formatter.format(
      (item.priceCents * entry.quantity) / 100
    );

    cartItemContainer.appendChild(cartItem);
  });
}
